<template>
  <v-text-field solo label="Крутой фильтр" v-model="filterStr">
    <template v-slot:append>
      <MyDialog width="700" title="Фильтербильдер" :loading="!filterParams">
        <template v-slot:activator="{ on, attrs }">
          <v-icon v-bind="attrs" v-on="on">mdi-application</v-icon>
        </template>
          <v-card-text v-if="filterParams">
            <v-text-field
              v-for="item in filterParams.fields.filter(v => !filterParams.slist[v])"
              :key="item"
              :label="item"
              dense
              v-model="newFilter[item]"
            />
            <v-combobox
              v-for="(item, key) in filterParams.slist"
              :key="key"
              :label="key"
              dense
              multiple
              small-chips
              v-model="newFilterSlist[key]"
              :items="item.map(v => v[0])"
            />
          </v-card-text>
          <v-card-actions>
            <v-btn color="primary" @click="apply">Применить</v-btn>
          </v-card-actions>
      </MyDialog>
    </template>
  </v-text-field>
</template>

<script>
import MyDialog from '@/render/components/Common/MyDialog';

export default {
  components: { MyDialog },
  props: ['func', 'value'],
  data: () => ({
    filterStr: '',
    newFilter: {},
    newFilterSlist: {}
  }),

  mounted() {
    this.filterStr = Object.entries(this.value).map(v => `${v[0]}="${v[1]}"`).join(' ');
  },

  methods: {
    h() {alert('хуй' + this.filterParams)},
    apply() {
      const getValue = (k, v) => {
        const found = this.filterParams.slist[k].find(b => b[0] === v);
        return found && found[1];
      };
      this.$emit('update:value', Object.fromEntries(Object.entries({
        ...Object.fromEntries(Object.entries(this.newFilterSlist).map(v => [v[0], v[1].map(b => getValue(v[0], b)).join(',')])),
        ...this.newFilter
      }).filter(v => !!v[1] && v[1] !== '')));
    }
  },

  watch: {
    value() {
      this.filterStr = Object.entries(this.value).map(v => `${v[0]}="${v[1]}"`).join(' ');
    }
  },

  asyncComputed: {
    async filterParams() {
      const getSlv = v => [v['#text'], v.attr['@_key']];
      const result = await this.api.func(`${this.func}.filter`).then(v => ({
        fields: Object.entries(v).filter(v1 => typeof v1[1] === 'string').map(v1 => v1[0]),
        slist: Object.fromEntries(v.slist.map(v1 => [v1.attr['@_name'], [...new Set(Array.isArray(v1.val) ? v1.val.map(b => getSlv(b)) : [getSlv(v1.val)])]]))
      }));

      return result;
    }
  }
};
</script>
